/*
*
* ==========================================
* GENERAL
* ==========================================
*
*/
.text-small {
	font-size: 0.85rem !important;
}

.text-gray {
	color: #aaa !important;
}

strong {
	font-weight: 700;
}

button:focus,
button:active {
	outline: none;
}

/*
*
* ==========================================
* NAVBAR
* ==========================================
*
*/
.navbar {
	position: relative;
}

.navbar .dropdown-menu {
	border-top: 2px solid #dcb14a;
}

.navbar .dropdown-menu::before {
	content: '';
	display: block;
	border-left: 0.5rem solid transparent;
	border-right: 0.5rem solid transparent;
	border-bottom: 0.5rem solid #dcb14a;
	position: absolute;
	top: -0.5rem;
	left: 1rem;
}

@media (min-width: 992px) {
	.navbar-brand {
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
	}
}

.navbar .dropdown-menu {
	visibility: hidden;
	opacity: 0;
	-webkit-transform: translateY(0.5rem);
	transform: translateY(0.5rem);
	transition: 0.2s ease all;
}

@media (min-width: 992px) {
	.navbar .dropdown-menu {
		display: block;
	}
}

@media (max-width: 991.98px) {
	.navbar .dropdown-menu.always-animated {
		display: block;
	}
}

.navbar .dropdown-menu.show {
	display: block;
	visibility: visible;
	opacity: 1;
	-webkit-transform: translateY(0px);
	transform: translateY(0px);
	transition: 0.2s ease all;
}

.megamenu {
	position: static;
}

.megamenu .dropdown-menu {
	width: 100%;
	z-index: 999;
}

/*
*
* ==========================================
* HERO
* ==========================================
*
*/
.hero {
	min-height: 30rem;
}

/*
*
* ==========================================
* CATEGORY
* ==========================================
*
*/
.category-item {
	display: block;
	position: relative;
	color: #343a40;
	transition: all 0.3s;
}

.category-item img {
	transition: all 0.3s;
}

.category-item-title {
	display: inline-block;
	padding: 0.5rem 1rem;
	background: #fff;
	text-transform: uppercase;
	letter-spacing: 0.07em;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.07);
	font-size: 0.8rem;
}

.category-item:hover img {
	opacity: 0.7;
}

/*
*
* ==========================================
* PRODUCT
* ==========================================
*
*/
.product img {
	-webkit-filter: grayscale(40%);
	filter: grayscale(40%);
	transition: all 0.3s;
}

.product-overlay {
	width: 100%;
	position: absolute;
	left: 0;
	bottom: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: end;
	align-items: flex-end;
	-ms-flex-pack: center;
	justify-content: center;
	padding: 1rem 0;
	opacity: 0;
	transition: all 0.3s;
}

.product-overlay ul li {
	transition: all 0.3s;
}

.product-overlay ul li:first-of-type {
	-webkit-transform: translateX(-10px);
	transform: translateX(-10px);
	opacity: 0;
}

.product-overlay ul li:last-of-type {
	-webkit-transform: translateX(10px);
	transform: translateX(10px);
	opacity: 0;
}

.product:hover img {
	opacity: 0.3;
}

.product:hover .product-overlay {
	opacity: 1;
}

.product:hover .product-overlay li {
	opacity: 1;
	-webkit-transform: none;
	transform: none;
}

.product .badge {
	position: absolute;
	top: 1rem;
	right: 1rem;
	z-index: 999;
}

/*
*
* ==========================================
* PRODUCT MODAL
* ==========================================
*
*/
.product-view {
	min-height: 20rem;
}

.quantity {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	align-items: center;
}

.quantity input {
	width: 2rem;
	text-align: center;
}

.quantity button {
	background: none;
	border: none;
	width: 1rem;
	outline: none;
}

/*
*
* ==========================================
* FOOTER
* ==========================================
*
*/
.footer-link {
	font-size: 0.85rem;
	position: relative;
	color: #999;
	transition: all 0.3s;
}

.footer-link:hover,
.footer-link:focus {
	padding-left: 0.6rem;
	color: #fff;
	text-decoration: none;
}

.footer-link:hover::before,
.footer-link:focus::before {
	opacity: 1;
}

.footer-link::before {
	content: '\f0da';
	display: inline-block;
	font-weight: 900;
	position: absolute;
	top: 50%;
	left: 0;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	color: #fff;
	transition: all 0.3s;
	opacity: 0;
}

