*/
.noUi-target {
	background: #efefef;
	border-radius: 0;
	border: none;
	box-shadow: none;
}

.noUi-horizontal {
	height: 2px;
}

@media (max-width: 991.98px) {
	.noUi-horizontal {
		height: 3px;
	}
}

.noUi-horizontal .noUi-handle {
	width: 10px;
	height: 10px;
	right: -10px;
